// CUSTOM FONT ICON LIBRARY
// Imports only the fontawesome icons we need using icomoon free
@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?xkh32k');
  src:  url('fonts/icomoon.eot?xkh32k#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?xkh32k') format('truetype'),
    url('fonts/icomoon.woff?xkh32k') format('woff'),
    url('fonts/icomoon.svg?xkh32k#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="uc-"], [class*=" uc-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.uc-envelope-o:before {
  content: "\f003";
}
.uc-envelope:before {
  content: "\f0e0";
}
.uc-bolt:before {
  content: "\e902";
}
.uc-edit:before {
  content: "\e903";
}
.uc-send-o:before {
  content: "\e904";
}

//VARIABLES
$grey: (
  dark: #4c4c4c,
  primary: #ccc,
  light: #f7f8fa,
  border: #f3f4f5,
);

$yellow: (
  primary: #FFEA88,
);

$blue: (
  primary: #509DE8,
);

//APP

.user-chat-box {
  width: 100%;
  font-family: 'Open Sans', sans-serif !important;

  .user-chat-box-new-message-badge-wrapper {
    position: absolute;
    left: 50%;

    .user-chat-box-new-message-badge {
      background-color: map-get($grey, primary);
      color: #fff;
      border-radius: 500px;
      text-align: center;
      padding: 3px 5px;
      font-size: 11px;
      width: auto;
      margin-top: -10px;
      left: -50%;
      position: relative;
      cursor: pointer;
    }
  }

  .user-chat-box-header {
    background-color: map-get($blue, primary);
    padding: 15px;
    color: #fff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    font-weight: 100;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    max-width: 100%;

    .user-chat-box-subtitle {
      font-size: 12px;
      color: #fff;
    }

    .user-chat-box-branding {
      font-size: 11px;

      i {
        vertical-align: middle;
      }

      a {
        color: #fff;
        text-decoration: none;
      }
    }
  }

  .user-chat-box-body {
    height: auto;
    overflow-y: scroll;
    border: 1px solid map-get($grey, border);
    border-bottom: 0px;
    max-width: 100%;

    .user-chat-box-message {
      padding: 30px 15px;
      border-bottom: 1px solid map-get($grey, border);
      font-size: 13px;
      font-weight: 200;
      width: auto;
      display: flex;
      align-items: center;
      word-break: break-all;

      img {
        max-width: 80%;
      }

      p {
        margin: 0px;
      }

      &:nth-child(odd) {
        background-color: map-get($grey, light);
      }

      .user-chat-box-circle-image img {
        border-radius: 50%;
        float: left;
        margin-right: 15px;
      }

      .user-chat-box-user-message-container {
        width: 100%;
      }

      .user-chat-box-user-message-header {
        width: 100%;
      }

      .user-chat-box-user-message-timestamp {
        float: right;
        font-size: 11px;
      }

      .user-chat-box-user-message-body{
        margin-top: 5px;

        .user-chat-box-user-message {
          p {
            float: left;
            width: 78%;
            word-break: break-word;
          }

          textarea {
            border: 0px;
            float: left;
            width: 68%;
            padding: 10px;
            font-size: 13px;
            font-weight: 100;
            height: auto;
            resize: none;
            word-break: break-word;
          }
        }

        .user-chat-box-user-message {
          .user-chat-box-user-message-edit-textarea {
            border: 1px solid map-get($grey, border);
            resize: none;
            margin: 0px;
          }
        }

        .user-chat-box-user-message-controls {
          float: right;
          width: 20%;

          .uc-edit {
            color: map-get($grey, primary);
            float: right;
            cursor: pointer;
          }
        }
      }
    }
  }

  .user-chat-box-messenger {
    background-color: #f4f7f9;
    max-width: 100%;
    height: auto;
    position: relative;
    display: flex;
    flex-direction: column;
    border: 1px solid map-get($grey, border);

    .user-chat-box-messenger-textarea-controls {
      position: absolute;
      color: map-get($grey, primary);
      right: 0px;
      top: 13px;
      resize: none;

      i {
        margin-right: 5px;
        cursor: pointer;
        z-index: 1;
      }

      .uc-envelope-o {
        &.active {
          color: map-get($blue, primary);
        }
      }

      i:last-of-type {
        margin-right: 15px;
        cursor: pointer;
        z-index: 1;
      }

      .uc-send-o {

        &.active {
          color: map-get($blue, primary);
        }
      }
    }

    .user-chat-box-messenger-textarea {
      border-top: 2px solid map-get($grey, border);
      resize: none;
      background-color: #f7f8fa;
      border: 0px;
      outline: none;
      padding: 13px 75px 13px 13px;
      font-weight: 100;
      line-height: 18px;
      font-size: 14px;
      max-width: 100%;
      height: auto;
      margin-top: 0px;
      margin-right: 0px;
      margin-bottom: 0px;
      margin-left: 0px;

      &[disabled] {
        background-color: map-get($yellow, primary);
        color: black;
      }

      &[disabled]::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: map-get($grey, dark);
        font-weight: 100;
      }
      &[disabled]::-moz-placeholder { /* Firefox 19+ */
        color: map-get($grey, dark);
        font-weight: 100;
      }
      &[disabled]:-ms-input-placeholder[disabled] { /* IE 10+ */
        color: map-get($grey, dark);
        font-weight: 100;
      }
      &[disabled]:-moz-placeholder[disabled] { /* Firefox 18- */
        color: map-get($grey, dark);
        font-weight: 100;
      }

      &:focus {
        background-color: #fff;
      }

      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: map-get($grey, primary);
        font-weight: 100;
        vertical-align: middle;
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: map-get($grey, primary);
        font-weight: 100;
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: map-get($grey, primary);
        font-weight: 100;
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: map-get($grey, primary);
        font-weight: 100;
      }
    }
  }
}
